import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams, Link, useNavigate } from "react-router-dom";
import "./details.css";

const Details = (props) => {
  let { idd } = useParams();

  const { Products } = props;
  const [state, setState] = useState([]);
  let selected;
  async function get() {
    selected = await Products.find((item) => item._id === idd);

    if (selected) {
      const newData = { ...state };
      newData._id = selected._id;
      newData.name = selected.name;
      newData.containes = selected.containes;
      newData.desc = selected.desc;
      newData.image = selected.image;
      setState(newData);
    } else {
      async function fetchData() {
        try {
          const { data } = await axios.get(
            "https://aim-server-back.herokuapp.com/api/aim/notes/"
          );
          selected = await data.find((item) => item._id === idd);

          const newData = { ...state };
          newData._id = selected._id;
          newData.name = selected.name;
          newData.containes = selected.containes;
          newData.desc = selected.desc;
          newData.image = selected.image;
          setState(newData);
        } catch (error) {
          console.log(error);
        }
      }
      fetchData();
    }
  }

  useEffect(() => {
    get();
  }, []);

  function deletePro(e) {
    e.preventDefault();
    axios
      .delete(url)
      .then((res) => {
        console.log(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  const navigate = useNavigate();

  const url = `https://aim-server-back.herokuapp.com/api/aim/notes/${state._id}`;

  function deletePro(e) {
    e.preventDefault();
    axios
      .delete(url)
      .then((res) => {
        console.log(res.data);
      })
      .catch((err) => {
        console.log(err);
      });

    navigate("/");
  }

  return (
    <div className="main-details container">
      <div className="row details-one">
        <div className="col-sm-12 col-md-6 details-left">
          <h2 className="details-name">{state.name}</h2>
          <h6 className="details-containes">{state.containes}</h6>
          <p className="details-desc">{state.desc}</p>
        </div>
        <div className="col-sm-12 col-md-6 details-right">
          <img src={state.image} alt={state.name} />
        </div>
      </div>
      <div className="btns">
        <div className="col-6 btn-left">
          <button className="btn btn-danger m-3" onClick={(e) => deletePro(e)}>
            Delete
          </button>
        </div>
        <div className="col-6 btn-right">
          <Link
            to={`/update/${state._id}`}
            className="btn btn-warning text-light m-3"
          >
            Update
          </Link>
        </div>
      </div>
    </div>
  );
};

export default Details;
