import axios from "axios";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./add.css";

const Add = () => {
  const navigate = useNavigate();
  
  const url = "https://aim-server-back.herokuapp.com/api/aim/notes/";
  const [Items, setItems] = useState({
    name: "",
    containes: "",
    desc: "",
    image: "",
  });

  function handle(e) {
    const newData = {...Items};
    newData[e.target.id] = e.target.value;
    setItems(newData);
    
  }

  function submit(e) {
    e.preventDefault();
    axios.post(url, {
      name: Items.name,
      containes:Items.containes ,
      desc:Items.desc,
      image:Items.image 
    }).then(res=>{
      console.log(res.data);
    })
    navigate("/");
  }


  return (
    <div className="container main-add">
      <form onSubmit={(e) => submit(e)}>
        <h2 className="h-add">Add your Dish</h2>
        <div className="form-group">
          <label className="add-name" for="name">
            Title:
          </label>
          <input
            onChange={(e) => handle(e)}
            value={Items.name}
            type="text"
            className="form-control"
            id="name"
            placeholder="Beef, Burger, Rosti..."
          />
        </div>
        <div className="form-group">
          <label className="add-containes" for="containes">
            Main Ingredients:
          </label>
          <input
            onChange={(e) => handle(e)}
            value={Items.containes}
            type="text"
            className="form-control"
            id="containes"
            placeholder="Tomato, Meat, Milk..."
          />
        </div>
        <div className="form-group add-description">
          <label for="desc">Description:</label>
          <textarea
            onChange={(e) => handle(e)}
            value={Items.desc}
            className="form-control"
            id="desc"
            rows="4"
            placeholder="Write your recipe here please!"
          ></textarea>
        </div>

        <div className="form-group">
          <label className="add-image" for="image">
            Image URL:
          </label>
          <input
            onChange={(e) => handle(e)}
            value={Items.image}
            type="url"
            className="form-control"
            id="image"
            placeholder="Add image URL here please!"
          />
        </div>
        <button type="submit" class="btn btn-primary mb-2">
          Add
        </button>
      </form>
    </div>
  );
};

export default Add;
