import axios from "axios";
import React, { useEffect } from "react";
import Slide from "../Slide/Slide";
import "./home.css";

const Home = (props) => {
  const { Products } = props;

  return (
    <div className="container p-3 homo">
      <div className="home-main row">
        {Products.map((item) => {
          return <Slide key={item._id} details={item} Products={Products}/>;
          
        })}
        
      </div>

      
    </div>
  );
};

export default Home;
