import React, { useEffect, useState } from "react";
import "./update.css";
import { useNavigate, useParams } from "react-router-dom";
import axios from "axios";

const Update = (props) => {
  let { idd } = useParams();

  const { Products } = props;

  const [Items, setItems] = useState({
    name: "",
    containes: "",
    desc: "",
    image: "",
  });
  const [firstCopy, setFirstCopy] = useState({
    name: "",
    containes: "",
    desc: "",
    image: "",
  });

  const navigate = useNavigate();
  let selected;

  // let selected = Products.find((item) => item._id === idd);

  // const { _id, name, containes, desc, image } = selected;
  const url = `https://aim-server-back.herokuapp.com/api/aim/notes/${Items._id}`;

  function handle(e) {
    const newData = { ...Items };

    newData[e.target.id] = e.target.value;
    setItems(newData);
  }

  function submit(e) {
    e.preventDefault();
    axios
      .put(url, {
        name: Items.name,
        containes: Items.containes,
        desc: Items.desc,
        image: Items.image,
      })
      .then((res) => {
        console.log(res.data);
      });
    navigate("/");
  }

  async function get() {
    selected = await Products.find((item) => item._id === idd);

    if (selected) {
      const newData = { ...Items };
      newData._id = selected._id;
      newData.name = selected.name;
      newData.containes = selected.containes;
      newData.desc = selected.desc;
      newData.image = selected.image;

      const newFirstCopy = { ...firstCopy };

      newFirstCopy.name = selected.name;
      newFirstCopy.containes = selected.containes;
      newFirstCopy.desc = selected.desc;
      newFirstCopy.image = selected.image;

      setFirstCopy(newFirstCopy);
      setItems(newData);
    } else {
      console.log("2");
      async function fetchData() {
        try {
          const { data } = await axios.get(
            "https://aim-server-back.herokuapp.com/api/aim/notes/"
          );
          selected = await data.find((item) => item._id === idd);

          const newData = { ...Items };
          newData._id = selected._id;
          newData.name = selected.name;
          newData.containes = selected.containes;
          newData.desc = selected.desc;
          newData.image = selected.image;

          const newFirstCopy = { ...firstCopy };
          newFirstCopy.name = selected.name;
          newFirstCopy.containes = selected.containes;
          newFirstCopy.desc = selected.desc;
          newFirstCopy.image = selected.image;

          setFirstCopy(newFirstCopy);
          setItems(newData);
        } catch (error) {
          console.log(error);
        }
      }
      fetchData();
    }
  }

  useEffect(() => {
    get();
  }, []);

  return (
    <div className="container main-update">
      <h2 className="h-update">Update your Dish</h2>
      <form onSubmit={(e) => submit(e)}>
        <div className="form-group">
          <label className="update-name" htmlFor="name">
            Title:
          </label>
          <input
            onChange={(e) => handle(e)}
            value={Items.name}
            type="text"
            className="form-control"
            id="name"
            placeholder="Beef, Burger, Rosti..."
          />
          <p
            style={{
              textAlign: "left",
              paddingLeft: ".5rem",
              paddingTop: ".25rem",
              color: "red",
              fontWeight: "400",
              fontSize: ".8rem",
              opacity: ".9",
            }}
          >
            Old Value : <span style={{ color: "#666" }}>{firstCopy.name}</span>
          </p>
        </div>
        <div className="form-group">
          <label className="update-containes" htmlFor="containes">
            Main Ingredients:
          </label>
          <input
            onChange={(e) => handle(e)}
            value={Items.containes}
            type="text"
            className="form-control"
            id="containes"
            placeholder="Tomato, Meat, Milk..."
          />
          <p
            style={{
              textAlign: "left",
              paddingLeft: ".5rem",
              paddingTop: ".25rem",
              color: "red",
              fontWeight: "400",
              fontSize: ".8rem",
              opacity: ".9",
            }}
          >
            Old Value :{" "}
            <span style={{ color: "#666" }}>{firstCopy.containes}</span>
          </p>
        </div>
        <div className="form-group update-description">
          <label htmlFor="desc">Description:</label>
          <textarea
            onChange={(e) => handle(e)}
            value={Items.desc}
            className="form-control"
            id="desc"
            rows="4"
            placeholder="Write your recipe here please!"
          ></textarea>
          <p
            style={{
              textAlign: "left",
              paddingLeft: ".5rem",
              paddingTop: ".25rem",
              color: "red",
              fontWeight: "400",
              fontSize: ".8rem",
              opacity: ".9",
            }}
          >
            Old Value : <span style={{ color: "#666" }}>{firstCopy.desc}</span>
          </p>
        </div>

        <div className="form-group">
          <label className="update-image" htmlFor="image">
            Image URL:
          </label>
          <input
            onChange={(e) => handle(e)}
            value={Items.image}
            type="url"
            className="form-control"
            id="image"
            placeholder="Add image URL here please!"
          />
          <p
            style={{
              textAlign: "left",
              paddingLeft: ".5rem",
              paddingTop: ".25rem",
              color: "red",
              fontWeight: "400",
              fontSize: ".8rem",
              opacity: ".9",
            }}
          >
            Old Value : <span style={{ color: "#666" }}>{firstCopy.image}</span>
          </p>
        </div>
        <button type="submit" className="btn btn-primary mb-2">
          Update
        </button>
      </form>
    </div>
  );
};

export default Update;
