import axios from "axios";
import React, { useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import "./slide.css";

const Slide = (props) => {
  const { details } = props;
  const { _id, name, containes, desc, image } = details;

  let shortenDesc = desc.slice(0, 100);
  const url = `https://aim-server-back.herokuapp.com/api/aim/notes/${_id}`;

  function deletePro(e) {
    e.preventDefault();
    axios
      .delete(url)
      .then((res) => {
        console.log(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  return (
    <div className="main-slide col-xs-12 col-sm-6 col-lg-3 ">
      <div className="card">
        <Link className="linko" to={`/product/${_id}`}>
          <div className="img-cont">
            <img className="card-img-top" src={image} alt="Card image cap" />
          </div>
        </Link>
        <div className="card-body">
          <h2 className="card-title">{name}</h2>
          <h6>Containes:{containes}</h6>
          <p className="card-text">
            Description: {shortenDesc}
            <Link className="linko" to={`/product/${_id}`}>
              {" "}
              <span className="spano">...etc.</span>
            </Link>
          </p>
          <form>
            <button
              className="btn btn-primary"
              type="submit"
              onClick={(e) => deletePro(e)}
            >
              Delete
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Slide;
